module.exports = function (Component) {
  Component.options.__i18n = Component.options.__i18n || []
  Component.options.__i18n.push('{"en":{"logout":"Log out","failed":"Failed to fetch main menu: {msg}"},"ru-RU":{"logout":"Выйти","failed":"Ошибка получения главного меню: {msg}"}}')
  delete Component.options._Ctor
}
