module.exports = function (Component) {
  Component.options.__i18n = Component.options.__i18n || []
  Component.options.__i18n.push('{"en":{"failed":"Failed to fetch user view: {msg}","goto_nav":"Back to the top"},"ru-RU":{"failed":"Ошибка получения пользовательского вида: {msg}","goto_nav":"Вернуться на главную"}}')
  delete Component.options._Ctor
}
